const cartItemsReducer = (state = [], action) => {
    switch(action.type) {
        case 'ADD_ITEM':
            return [...state, action.payload];
        case 'DELETE_ITEM':
            return state.filter(item => {
                return item.id !== action.payload;
            });
        default:
            return state;
    }
};

export default cartItemsReducer;